html {
  height: 100%;
  background: #333 url("https://img.freepik.com/photos-premium/chute-eau-vue-nuit-dans-jungle_635705-837.jpg") bottom / cover no-repeat;
  overflow: hidden;
}

input {
  color: rgba(255, 255, 255, .5);
  background: none;
  border: 2px solid rgba(255, 255, 255, .5);
  border-radius: .25em;
  padding: .25em .5em .3125em;
  font-size: 1.75em;
  transition: all .1s;
}

input:focus {
  border-color: rgba(255, 255, 255, .75);
  outline: none;
}

input.keyup {
  color: #fff;
  text-shadow: 0 0 .125em #fff;
  border-color: #fff;
  box-shadow: 0 0 .25em #fff, inset 0 0 .25em #fff;
}

.resultContainer {
  max-width: 600px;
  background-color: #f9f9f9;
  border-radius: 8px;
  margin: 20px auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.resultContainer h2 {
  color: #333;
  border-bottom: 2px solid #ccc;
  margin-bottom: 15px;
  padding-bottom: 10px;
  font-size: 1.8em;
}

.resultContainer p {
  color: #555;
  margin-bottom: 10px;
  font-size: 1.1em;
}

.resultContainer ul {
  color: #555;
  padding-left: 20px;
  list-style-type: disc;
}

.resultContainer li {
  margin-bottom: 5px;
  font-size: 1.1em;
}

.resultContainer img {
  max-width: 100%;
  height: auto;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 10px;
}

.find-country {
  z-index: 1;
  color: #000;
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 5px;
  outline: none;
  margin: 2% auto;
  padding: 10px;
  font-size: 16px;
  display: block;
  position: relative;
}

.particle {
  width: 8px;
  height: 8px;
  pointer-events: none;
  filter: blur(1px);
  opacity: 0;
  background: radial-gradient(circle, #fff 0%, rgba(255, 255, 255, 0) 70%);
  border-radius: 50%;
  animation: pixieDust 2s ease-out infinite;
  position: absolute;
}

@keyframes pixieDust {
  0% {
    opacity: 1;
    transform: translateX(0)translateY(0)scale(1);
  }

  100% {
    opacity: 0;
    transform: translateX(300px)translateY(-100px)scale(.1);
  }
}

